
.button {
  padding: 5px;
  margin: 25px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.button .social {
  height: 50px;
  width: 50px;
  margin: 0 5px;
  padding: 10px;
  border-radius: 50%;
  backdrop-filter: blur(10px);
  cursor: pointer;
  transition: background 0.2s ease;
  box-shadow: -5px 5px 25px ;
}

.button .social:hover {
  background-color: #5651e5;
  fill: var(--color-pri);
}

@media (max-width: 500px) {
  .button .social {
    height: 40px;
    width: 40px;
  }
}